@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&family=Zilla+Slab+Highlight:wght@700&display=swap);
*,*::before,*::after{box-sizing:border-box}html{position:fixed;left:0;right:0;width:100vw;height:100vh;margin:0;padding:0;touch-action:none;scroll-behavior:smooth}body{position:fixed;left:0;right:0;width:100vw;height:100vh;margin:0;background:linear-gradient(135deg, #000 25%, transparent 25%) -50px 0,linear-gradient(225deg, #000 25%, transparent 25%) -50px 0,linear-gradient(315deg, #000 25%, transparent 25%),linear-gradient(45deg, #000 25%, transparent 25%);background-size:2em 2em;background-color:#202020;font-family:Roboto,sans-serif}p{word-break:break-word}button,a{border:none;padding:0;cursor:pointer;background:none;color:#fff;text-decoration:none;font-size:1.5rem}#title-bar,#nav-bar{position:fixed;top:0;left:0;right:0;overflow:hidden;z-index:2}#title-bar{left:0;right:0;height:4rem;margin:0;background-color:#202020}#title-bar button,#title-bar img{position:fixed;top:.66667rem;left:4.5rem;z-index:999;font-family:"Zilla Slab Highlight",cursive;font-size:2rem}#title-bar img{top:.8rem;left:2rem}#nav-bar{position:fixed;display:block;width:50%;margin:auto;line-height:100%;text-align:center;transition:0s;font-size:1.5rem}#nav-bar a{display:-webkit-inline-flex;display:inline-flex;margin:0;padding:1.25rem;border-style:collapse;border-right:2px solid rgba(32,32,32,0.875)}#nav-bar a:first-of-type{border-left:2px solid rgba(32,32,32,0.875)}#nav-bar a:hover{background-color:rgba(64,64,64,0.75)}#nav-bar a.selected{background-color:rgba(64,64,64,0.75)}#nav-bar a.selected:before{position:absolute;display:block;top:.5rem;height:100%;content:".";font-family:Arial;-webkit-transform:scale(2);transform:scale(2)}#nav-bar.portrait{top:4rem;left:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0;margin:initial;transition:1s}#nav-bar.portrait a{display:-webkit-flex;display:flex;background-color:#202020}#nav-bar.portrait a:hover,#nav-bar.portrait a.selected{background-color:#404040}#nav-bar.portrait.closed{right:-10rem}#nav-bar.portrait.open{right:0rem;transition:1s}#menu-opener{position:fixed;top:.75rem;right:1.5rem;font-size:.5rem}.canvas-container{position:fixed;top:0;width:100vw;height:100vh}.page{position:fixed;display:none;top:4rem;bottom:0;width:100vw;overflow-x:hidden;overflow-y:scroll;z-index:1;background-color:#fff;padding-top:3em;padding-left:calc(min(10em, 10vw));padding-right:calc(min(10em, 10vw));font-size:1rem;font-weight:300;line-height:2rem;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased}.page.fade,.page.active{display:block;transition:opacity .3s linear;-moz-transition:opacity .3s linear;-webkit-transition:opacity .3s linear}.page.fade{opacity:0}.page.active{opacity:1}

